  animation-delay: 0.1s;
}
.app-container {
  opacity: 0;
  transition: opacity 0.5s ease;
}
.app-loaded {
  opacity: 1;
}
.stagger-animation > * {
  opacity: 0;
  transform: translateY(20px);
}
.stagger-animation.animate > *:nth-child(1) {
  animation: fadeInUp var(--animation-duration-medium) var(--animation-easing-smooth) forwards;
  animation-delay: calc(var(--animation-delay-stagger) * 1);
}
.stagger-animation.animate > *:nth-child(2) {
