  animation-delay: calc(var(--animation-delay-stagger) * 3);
}
.stagger-animation.animate > *:nth-child(4) {
  animation: fadeInUp var(--animation-duration-medium) var(--animation-easing-smooth) forwards;
  animation-delay: calc(var(--animation-delay-stagger) * 4);
