  animation-delay: calc(var(--animation-delay-stagger) * 6);
}
.pulse {
  animation: pulse 2s infinite;
}
.floating {
  animation: floating 3s ease-in-out infinite;
}
.shimmer {
  position: relative;
  overflow: hidden;
}
.shimmer::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: translateX(-100%);
  background-image: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0,
    rgba(255, 255, 255, 0.2) 20%,
    rgba(255, 255, 255, 0.5) 60%,
    rgba(255, 255, 255, 0)
  );
  animation: shimmer 2s infinite;
}
.animated-button {
  position: relative;
  overflow: hidden;
  transition: all 0.3s var(--animation-easing-spring);
  z-index: 1;
}
.animated-button::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
  background-color: var(--color-primary-600);
  transition: height 0.3s var(--animation-easing-spring);
  z-index: -1;
}
.animated-button:hover {
  color: white !important;
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}
.animated-button:hover::before {
  height: 100%;
}
.animated-button:active {
  transform: translateY(-1px) scale(0.98);
}
.animated-button-secondary {
  position: relative;
  overflow: hidden;
  transition: all 0.3s var(--animation-easing-spring);
  z-index: 1;
}
.animated-button-secondary::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
  background-color: var(--color-secondary-600);
  transition: height 0.3s var(--animation-easing-spring);
  z-index: -1;
}
.animated-button-secondary:hover {
  color: var(--color-secondary-foreground) !important;
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}
.animated-button-secondary:hover::before {
  height: 100%;
}
.animated-button-secondary:active {
  transform: translateY(-1px) scale(0.98);
}
.card-hover {
  transition: all 0.4s var(--animation-easing-spring);
}
